
.HomePage {
  display: flex;
  flex-direction: column;
  background-color: #E6E9F2;
  align-items: center;
  justify-content: space-between;
  padding-top: 30px;
  padding-bottom: 64px;
  overflow: hidden;
  height: 100vh;
}

.Main {
  display: flex;
  // height: calc(100vh - 165px);
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: white;
  overflow: hidden;
  max-width: 1400px;
  margin-bottom: 30px;
}


@media (min-width: 414px) and (max-width: 1440px) {
  .HomePage {
    padding: 9px;
  }  
}
