
.ArticlePage {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 56px auto 162px auto;

  @media (min-width: 414px) and (max-width: 1440px) {
    width: 414px;
    padding: 24px 15px 37px;
  }
  
  &__container {
    display: flex;
    flex-direction: row;
    gap: 115px;

    @media (min-width: 414px) and (max-width: 1440px) {
      flex-direction: column;
      gap: 0;

    }
  }

  &__container2 {
    width: 721px;

    @media (min-width: 414px) and (max-width: 1440px) {
      width: 414px;
    }
  }

  &__Title {
    display: flex;
    flex-direction: row;

    @media (min-width: 414px) and (max-width: 1440px) {
      flex-direction: column;
    }
  }

  &__map-title {
    display: none;

    @media (min-width: 414px) and (max-width: 1440px) {
      display: block;
    }
  }

  &__btn-blue {
    margin-top: 50px;
    margin-bottom: 32px;
  }

  &__container-title {
    display: flex;
    flex-direction: row;

    @media (min-width: 414px) and (max-width: 1440px) {
      width: 363px;
      flex-direction: column;
    }
  }

  &__title {
    width: 401px;
    font-family: 'Proxima Nova';
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 30px;
    letter-spacing: -0.75px;
    color: #3A4562;

    @media (min-width: 414px) and (max-width: 1440px) {
      width: 363px;
    }
  }

  &__btn {
    margin-top: 30px;
    margin-bottom: 160px;
    margin-left: -1300px;

    @media (min-width: 414px) and (max-width: 1440px) {
      margin: 30px auto 160px;
    }
  }

  &__map {
    
  }
  
  &__map-google {

    @media (min-width: 414px) and (max-width: 1440px) {
      margin: 0 auto;
      width: 372px;
      height: 436px;
    }
  }
}

