.TodoList {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 32px;
  background-color: #E6E9F2;

  @media (min-width: 414px) and (max-width: 1440px) {
    gap: 8px;
  }
}

.TodoItem {
  display: flex;
  align-items: center;
  max-width: 1400px;
  height: 164px;
  color: #3A4562;
  gap: 26px;
  padding: 24px 16px;
  background: #FFFFFF;
  box-shadow: 2px 1px 7px rgba(0, 0, 0, 0.08), 0px 2px 1px -1px rgba(0, 0, 0, 0.04), 0px 1px 3px rgba(0, 0, 0, 0.12);
  border-radius: 8px;

  @media (min-width: 414px) and (max-width: 1440px) {
    display: flex;
    max-width: 396px;
    gap: 0;
    flex-direction: column-reverse;
    padding: px;
  }
  
  &__container {
    display: flex;
    align-items: center;
    gap: 26px;
  }

  &__container2 {
    display: flex;
    @media (min-width: 414px) and (max-width: 1440px) {
    }
  }

  &__photo {
    min-width: 85px;
    height: 85px;

    @media (min-width: 414px) and (max-width: 1440px) {
      max-width: 66px;
      height: 66px;
    }
  }
  
  &__img {
    border-radius: 50%;
    width: 85px;
    height: 85px;

    @media (min-width: 414px) and (max-width: 1440px) {
      max-width: 66px;
      height: 66px;
    }
    
  }

  &__content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 0px;
    gap: 5 px;
    max-width: 812px;
    height: 116px;

    @media (min-width: 414px) and (max-width: 1440px) {
      max-width: 278px;
    }
  }
  
  &__title {
    font-family: 'Proxima Nova';
    text-align: start;
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 25px;
    letter-spacing: -0.625px;
    width: 512px;
    height: 50px;
    color: #3A4562;

    @media (min-width: 414px) and (max-width: 1440px) {
      max-width: 278px;
      font-size: 18px;
      font-weight: 400;
    }
  }

  &__name {
    display: flex;
    width: 1087px;
    height: 25px;
    font-family: 'Proxima Nova';
    font-style: normal;
    align-items: flex-start;
    font-weight: 400;
    font-size: 16px;
    line-height: 25px;
    letter-spacing: 0.23619px;
    color: #878D9D;
  }

  &__posted-block {
    display: flex;
    flex-direction: column;
    gap: 50px;
    font-family: 'Proxima Nova';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 25px;
    min-width: 150px;
    align-items: flex-end;
    text-align: right;
    letter-spacing: 0.23619px;
    color: #878D9D;

    @media (min-width: 414px) and (max-width: 1440px) {
      font-size: 14px;
    }
  }
}

@media (min-width: 414px) and (max-width: 1440px) {
  .w-6 {
    display: none;
  }
}
